  .banner-text{
    background-color: black;
    opacity: .8;
    width: 90%;
    height: 40%;
    border-radius: 10px;
    margin: auto;
    box-shadow: 5px 12px 22px black;
    align-items: center;
  justify-content: center;
  font-size: 30px;
   
  }

.landing-grid{
  background: #005C97;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #363795, #005C97);  /* Chrome 10-25, Safari 5.1-6 */
